

a {
    text-decoration: none;
    color: black;
    cursor: pointer;
    z-index: 1;
}

.icon {
    height:20px;
    width:20px;
    cursor: pointer;  
}

/* TOP HEADER */
.topHeader {
    height: 70px;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
} 

/* LEFT SIDE CONTENT LINKS BAR */

.petfinderLogo{
    color: #6504b5;
    margin: auto 43px;
    font-size: 28px;
}

.leftHeader {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align:center;
        align-items:center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 75%;
}

.contentLinks{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: row;
        flex-direction: row;
    color: rgb(91, 88, 88);
}

.contentLinks > div{
    margin: auto 30px auto 0px;
    font-weight: bold;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
}

.contentLinks >div >img {
    z-index: -1;
    margin: auto 0px 6px 15px;
}

.searchDiv{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    margin-right: 10px;
}

.searchDiv > input {
    margin-right: 5px;
    padding: 5px 10px;
    height: 40px;
    border-radius: 20px;
    border-style: solid;
    font-size: 18px;
    width: 15vw;
    line-height: 5px;
}


.searchDiv > input:focus {
    outline: none; 
}

.signInLinks {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 25%;
    border-left: 1px rgb(179, 177, 177) solid; 
}

.signInLinks > p {
    margin: auto 0px auto 30px;
    cursor: pointer;
    font-size: 18px;
}

/* BOTTOM HEADER */

.bottomHeader {
    height: 70px;
    width: 100%;
    background-color:  #6504b5;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    color: white;
}

.infoDiv{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    width: 75%;
    /* border: 1px white solid; */
}

.nearYouDiv{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: left;
        justify-content: left;
    -ms-flex-align: center;
        align-items: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.nearYouDiv p{
    text-align: center;
    margin-right: 15px;
    padding: 5px 0px;
    min-width: 100px;
}

.bigIcon {
    height: 35px;
    margin: auto 50px auto 50px;
}

.saveSearchDiv {
    width: 25%;
    display: -ms-flexbox;
    display: flex;
}  





.saveSearchDiv > button {
    margin: auto;
    padding: 7px 30px;
    border-radius: 30px;
    background-color: #6504b5;
    color:white;
    font-size: 18px;
    font-weight: 500;
    border-style: solid;
    border-color: white;
    cursor: pointer;
}

.saveSearchDiv > button:hover {
    background-color: white;
    color: #6504b5;
}

.saveSearchDiv > button:focus {
    outline: none; 
}

.options {
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
}

.options > div{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-left: 8px;
}

.options >div >input {
    margin: 0px 5px 5px 0px;
}

.contentLinksMedium {
    display: none;
}

@media only screen and (max-width: 1000px){

    .contentLinks, .searchDiv, .saveSearchDiv{
        display: none;
    }

    .signInLinks{
        color:  #6504b5;
    }

    .contentLinksMedium {
        display: -ms-flexbox;
        display: flex;
    }
    .contentLinksMedium >img {
       width: 30px;
       height: 30px;
       margin: 0px 15px;
       cursor: pointer;
    }

    .petfinderLogo, .bigIcon{
        margin-left: 15px;
    }
}

@media only screen and (max-width: 700px){
    .signInLinks{
        display: none;
    }

    .leftHeader{
        width: 100%;
    }

    .infoDiv{
        width: 100%;
        -ms-flex-pack: justify;
            justify-content: space-between;
    }
} 


